body {
  margin: 0;
  display: grid;
  background-color: var(--background-color-primary);
  color: white;
}
body header {
  z-index: 1;
  height: 120px;
  color: white;
  display: flex;
  font-family: "Prompt";
  letter-spacing: 3px;
  justify-content: space-between;
}
body header.down-scroll {
  position: fixed;
  top: 0px;
  width: 100%;
  transition: top 0.3s;
  background-color: var(--background-color-primary-opacity);
  backdrop-filter: blur(5px);
}
body header img {
  cursor: pointer;
  height: 5rem;
  height: 5rem;
  margin-top: 1rem;
  margin-left: 4rem;
  transition: transform 0.3s;
}
body header img:hover {
  transform: scale(1.05);
}
body header .menu-container {
  width: 65%;
  display: flex;
  align-items: center;
}
body header .menu-container nav {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
}
body header .menu-container p {
  width: fit-content;
  padding: 0 15px;
  margin: 0.5rem 0;
  cursor: pointer;
}
body header .menu-container p a {
  margin: 0;
}
body header .menu-container p a:hover {
  transition: 0.5s;
  color: var(--blue-primary);
}
body header .nous-rejoindre {
  border: rgba(255, 255, 255, 0.699) solid 1px;
  letter-spacing: normal;
  height: fit-content;
  padding: 0 0 0 2rem;
  display: flex;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 3rem;
}
body header .nous-rejoindre, body header .nous-rejoindre * {
  cursor: pointer;
}
body header .nous-rejoindre span.point {
  background-color: var(--blue-primary);
  border-radius: 50%;
  height: 12px;
  width: 12px;
  margin-right: 10px;
  margin-top: auto;
  margin-bottom: auto;
}
body header .nous-rejoindre p {
  min-width: 10rem;
}
body header .nous-rejoindre:hover {
  transition: 0.2s;
  border: var(--blue-primary) solid 1px;
  color: var(--blue-primary);
}
body header .nous-rejoindre:hover span.point {
  transition: 1.2s;
  background-color: rgba(255, 255, 255, 0.699);
}
body .content {
  padding: 0 2.5rem 4rem 2.5rem;
}
body .content section {
  margin-top: 2rem;
  margin-bottom: 6rem;
}
body .content section .span-image {
  border-radius: 5px;
}
body footer {
  padding: 1rem 2rem;
}
body footer .footer-container {
  padding: 2rem 0;
  display: flex;
  flex-wrap: wrap;
}
body footer .footer-container div {
  margin-right: 6rem;
}
body footer .footer-container h3 {
  font-size: 1.8rem;
  max-width: 50px;
}
body footer .footer-container .footer-register {
  flex: 0.4;
}
body footer .footer-container .footer-register input {
  width: 99%;
